.login-form,
.create-profile__form {
  width: 100%;
}

.login-form__container,
.create-profile__container {
  display: flex;
  flex-direction: column;
  align-items: center;

  &-title {
    margin-bottom: 8px;
  }

  &-subtitle {
    max-width: 80%;
    margin-bottom: 16px;
    text-align: center;
  }
}

.logo {
  width: 80px;
  object-fit: cover;
  margin-bottom: 5%;
}

.create-profile__container-title {
  margin-bottom: 12px;
}

.create-profile__container {
  margin: 10% 0;
}

.get-started__actions {
  width: 90%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  padding-top: 32px;
  row-gap: 8px;

  .create-profile-button {
    width: 100%;
    text-decoration: none;

    button {
      width: 100%;
    }
  }

  .login-button {
    text-decoration: none;
  }
}
